'use client';

import React, { useState } from 'react';
import { Section, Container, Text, Input, Textarea, Button } from '../atoms';
import { Send } from 'lucide-react';
import styles from './Contact.module.css';

export interface ContactProps {
  sectionNumber?: string;
  headline?: string;
  description?: string;
}

export const Contact: React.FC<ContactProps> = ({
  sectionNumber = '04',
  headline = "Let's Build Something Together",
  description = 'Open to collaboration on research projects, open-source contributions, healthcare AI initiatives, or consulting opportunities.',
}) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });

  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    // TODO: Implement form submission logic
    console.log('Form submitted:', formData);
    
    // Simulate API call
    setTimeout(() => {
      setIsSubmitting(false);
      setFormData({ name: '', email: '', message: '' });
      alert('Message sent successfully!');
    }, 1000);
  };

  return (
    <Section id="contact" className={styles.contact}>
      <Container>
        <div className={styles.contactContent}>
          <div className={styles.contactHeader}>
            <div className={styles.contactTitle}>
              <Text variant="h2" className={styles.sectionNumber}>
                {sectionNumber}
              </Text>
              <Text variant="h2">{headline}</Text>
            </div>
            <Text variant="bodyLarge" color="secondary" className={styles.contactDescription}>
              {description}
            </Text>
          </div>

          <form className={styles.contactForm} onSubmit={handleSubmit}>
            <Input
              label="Name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
              placeholder="Your name"
            />
            <Input
              label="Email"
              name="email"
              type="email"
              value={formData.email}
              onChange={handleChange}
              required
              placeholder="your.email@example.com"
            />
            <Textarea
              label="Message"
              name="message"
              value={formData.message}
              onChange={handleChange}
              required
              placeholder="Your message..."
            />
            <div className={styles.formActions}>
              <Button
                type="submit"
                variant="accent"
                icon={Send}
                iconPosition="right"
                disabled={isSubmitting}
              >
                {isSubmitting ? 'Sending...' : 'Send Message'}
              </Button>
            </div>
          </form>
        </div>

        <div className={styles.contactVisual}>
          <div className={styles.cursorIndicator}>
            <div className={styles.cursorDot} />
          </div>
        </div>
      </Container>
    </Section>
  );
};

